            let self = this
            console.log('reset input')
            self.searchItem = ''
            self.searchResult = []

            //   
            //self.categoryChecked = []
            //self.statusChecked = []
            //self.dataRange = ['','']

            // console.log( this.searchItem, this.searchResult)
            // 

            //url   

            self.detailIndex = 0
            self.fetchAllItems()
