            let Params = {
                'currentPage': self.currentPage,
                'pageSize': self.pageSize,
                'dataRange': encodeURIComponent(self.dataRange),
                'urlPart': self.searchItem.toLowerCase(),
                'categoryFilter': encodeURIComponent(self.categoryChecked),
                'statusFilter': encodeURIComponent(self.statusChecked)
            }
            self.fetchItems(Params)

        },
        singleSearch: function (item) {
            // search item with specifif uid ,so only one will return
            let self = this
            console.log('singleSearch')
            self.searchItem = item.rootUrl
            console.log('uid', item['_id']['$oid'])
            console.log('searchItem', self.searchItem)
            self.searchResult = [] //disapper options window
            //  single search

            self.categoryChecked = []
            self.statusChecked = []
            self.dataRange = ['', '']
            //   search

            self.currentPage = 1
            // let pageParams = {'currentPage':self.currentPage,'pageSize':self.pageSize, 'UrlId': item['_id']['$oid'] }
            //let Params = {'currentPage':self.currentPage,'pageSize':self.pageSize,'UrlId': item['_id']['$oid']}
            // let Params = {'currentPage':self.currentPage,'pageSize':self.pageSize,'dataRange':encodeURIComponent(self.dataRange),'UrlId': item['_id']['$oid'],'urlPart': '' ,'categoryFilter': encodeURIComponent(self.categoryChecked), 'statusFilter': encodeURIComponent(self.statusChecked)}
            let Params = {
                'currentPage': self.currentPage,
                'pageSize': self.pageSize,
                'UrlId': item['_id']['$oid']
            }
            self.fetchItems(Params)
            self.searchItem = '' // 
        },

