        urlSearch: function () {
            console.log('urlSearch running ...')
            //  
            // :  singleSearch
            let self = this
            let query = self.searchItem
            // console.log(query)
            // self.searchItem = query.toLowerCase()
            //console.log(this.searchItem)
            //, query  
            if (query !== '') {
