        fetchAllItems: function () {
            let self = this
            // let Params = {'currentPage':self.currentPage,'pageSize':self.pageSize}
            let Params = {
                'currentPage': self.currentPage,
                'pageSize': self.pageSize,
                'dataRange': encodeURIComponent(self.dataRange),
                'UrlId': '',
                'urlPart': self.searchItem.toLowerCase(),
                'categoryFilter': encodeURIComponent(self.categoryChecked),
                'statusFilter': encodeURIComponent(self.statusChecked)
            }
            self.fetchItems(Params)

        },

        fetchItems: function (getParams) {
            let self = this
            let Params = getParams
            self.loading = true
            self.axios({
                    method: 'get',
                    url: self.baseurl + 'Urls/' + self.currentComponent,

                    params: Params
                })
                .then(res => {
                    //console.log(res)
                    // self.currentPage = 1
                    if (res.data.count !== '') {
                        self.itemCount = res.data.count
                    }
                    self.seeDetailKey = self.seeDetailKey + 1
                    self.UrlItemData = res.data.content
                    self.loading = false
                })
                .catch(err => {
                    self.loading = false
                    console.log(err)
                    // self.$Message.error(err.response.data.detail);
                })
        },
        handleUrlDeleteItem: function (uidInfo) {
            let self = this
            let uid = uidInfo['uid']
            // 
            self.axios({
                    method: 'delete',
                    url: self.baseurl + 'Urls/' + self.currentComponent,

                    data: [uid]
                })
                .then(res => {
                    // console.log(res)
                    self.currentPage = 1
                    if (res.data.count !== '') {
                        self.itemCount = res.data.count
                    }
                    self.UrlItemData = res.data.content
                })
                .catch(err => {
                    // console.log(err)
                    console.log(err.response.data.detail)
                    // self.$Message.error(err.response.data.detail);
                })

        },
        handleUrlNewItem: function (itemInfo) {
            let self = this
            //   
            //  () 

            if (!itemInfo['uid']) {
                //
                self.currentPage = Math.ceil((self.itemCount + 1) / self.pageSize)
                let pageParams = {
                    'currentPage': self.currentPage,
                    'pageSize': self.pageSize
                }
                let totalData = self.okData
                if (itemInfo.data.length > 0) {
                    totalData = totalData.concat(itemInfo.data)
                }
                //console.log('totalData', totalData)
                // 
                //  

                //  totalData 
                if (totalData.length === 0) {
                    self.$Message.warning(',!');
                    return
                }
                self.showLoading = true
                self.axios({
                        method: 'post',
                        url: self.baseurl + 'Urls/' + self.currentComponent,
                        data: totalData,
                        params: pageParams
                    })
                    .then(res => {
                        // console.log(res)
                        // self.currentPage = 1
                        if (res.data.count !== '') {
                            self.itemCount = res.data.count
                        }
                        self.UrlItemData = res.data.content
                        self.showLoading = false
                        self.$Message.success(totalData.length + 'URL');
                        //self.formCustom = self.formCustomOrigin
                    })
                    .catch(err => {
                        // console.log(err)
                        self.showLoading = false
                        console.log(err.response.data.detail)
                        self.$Message.error({
                            content: JSON.stringify(err.response.data.detail),
                            duration: 0,
                            closable: true
                        });
                        //self.formCustom = self.formCustomOrigin
                        let Params = {
                            'currentPage': self.currentPage,
                            'pageSize': self.pageSize,
                            'dataRange': encodeURIComponent(self.dataRange),
                            'UrlId': '',
                            'urlPart': self.searchItem.toLowerCase(),
                            'categoryFilter': encodeURIComponent(self.categoryChecked),
                            'statusFilter': encodeURIComponent(self.statusChecked)
                        }
                        self.fetchItems(Params)
                    })
            } else {
                //console.log(itemInfo)
                // 
                let pageParams = {
                    'currentPage': self.currentPage,
                    'pageSize': self.pageSize
                }
                self.showLoading = true
                self.axios({
                        method: 'put',
                        url: self.baseurl + 'Urls/' + self.currentComponent + '/' + itemInfo.uid,
                        data: itemInfo['data'][0], //

                        params: pageParams
                    })
                    .then(res => {
                        // console.log(res)
                        // self.currentPage = 1
                        if (res.data.count !== '') {
                            self.itemCount = res.data.count
                        }
                        self.showLoading = false
                        self.UrlItemData = res.data.content
                    })
                    .catch(err => {
                        // console.log(err)
                        self.showLoading = false
                        console.log(err.response.data.detail)
                        self.$Message.error(err.response.data.detail);
                    })
            }

        },
