<template>
<div class="Urls-seeDetail">
    <i-modal v-model="DetailWindowShow" :title="'' + totalPage + '' +currentPage +'' + dataLength +'Url, ' + (currentIndex + 1) + 'url: '  + website2 + '  : ' + currentUrlStatus" width="95" :closable="false" :mask-closable="false" :scrollable="true" :styles="{top: '20px'}">
        <div class="Urls-seeDetail-main" id="iframe-parent">
            <iframe v-if="DetailWindowShow" :key="refreshKey" id='iframe' ref="iframe" class="Urls-seeDetail-iframe" :src="website2" frameborder="3" bordercolor=red></iframe>
            <div class="Urls-seeDetail-operate">
                <i-button class="Urls-seeDetail-operate-button" type="warning" size="large" :disabled="currentUrlStatus === ''" @click="setInvalid" icon="md-warning"></i-button>
                <i-button class="Urls-seeDetail-operate-button" type="success" size="large" :disabled="currentUrlStatus !== ''" @click="setValid" icon="md-medkit"></i-button>
                <i-button class="Urls-seeDetail-operate-button speccial2" type="primary" size="large" @click="refresh" icon="md-refresh"></i-button>
                <i-button class="Urls-seeDetail-operate-button speccial" type="info" size="default" @click="moveLast" :disabled="currentPage=== 1"></i-button>
                <i-button class="Urls-seeDetail-operate-button" type="info" size="large" shape="circle" @click="moveUp" :disabled="currentIndex === 0 && currentPage === 1"></i-button>
                <i-button class="Urls-seeDetail-operate-button" type="info" size="large" shape="circle" @click="moveDown" :disabled="currentIndex === (dataLength -1) &&  currentPage === totalPage"></i-button>
                <i-button class="Urls-seeDetail-operate-button" type="info" size="default" @click="moveNext" :disabled="currentPage === totalPage"></i-button>
            </div>
        </div>
        <div slot="footer">
            <i-button type="default" size="large" @click="modalCancel('')"></i-button>
        </div>
    </i-modal>
</div>
</template>

<script>
export default {
    name: 'seeDetail',
    data() {
        return {
            //disabledNow: false,
            refreshKey: 1,
            currentIndex: JSON.parse(JSON.stringify(this.detailIndex)),
            // website2: JSON.parse(JSON.stringify(this.website)),
            currentPage: JSON.parse(JSON.stringify(this.currentPageNum)),
            totalPage: JSON.parse(JSON.stringify(this.totalPageNum)),
        }
    },
    mounted() {},
    props: ['website', 'detailIndex', 'pageData', 'currentPageNum', 'totalPageNum', 'DetailWindowShow'],
    computed: {
        website2: function () {
            //console.log('this.pageData.length', this.pageData.length)
            if (this.pageData.length === 0) {
                // console.log('')
                return 'https://www.stockhey.com'
            } else {
                // console.log('this.pageData[this.currentIndex]',this.pageData,this.currentPage, this.currentIndex)
                // console.log(this.pageData, this.currentIndex)
                return this.pageData[this.currentIndex]['rootUrl']
            }
        },
        currentUrlStatus: function () {
            //console.log('this.pageData.length', this.pageData.length)
            if (this.pageData.length === 0) {
                // console.log('')
                return ''
            } else {
                return this.pageData[this.currentIndex]['status']
            }
        },
        dataLength: function () {
            //console.log('this.pageData.length', this.pageData.length)
            return this.pageData.length
        }
    },
    beforeDestroy() {},
    methods: {
        //...mapMutations(['changeDetailWindowShow']),
        refresh: function () {
            this.refreshKey = this.refreshKey + 1
        },
        setInvalid: function () {
            // bug     
            //  

            //   uid
            let self = this
            let uid = self.pageData[self.currentIndex]['_id']['$oid']
            //console.log(uid)
            // index index
            self.$emit('tagInvalid', {
                'uid': uid,
                'index': self.currentIndex
            })
        },
        setValid: function () {
            // bug     
            //  

            //   uid
            let self = this
            let uid = self.pageData[self.currentIndex]['_id']['$oid']
            //console.log(uid)
            // index index
            self.$emit('tagValid', {
                'uid': uid,
                'index': self.currentIndex
            })
        },
        modalOk() {

        },
        modalCancel() {
            //this.changeDetailWindowShow(false)
            this.$emit('update:DetailWindowShow', false)
        },
        moveLast: function () {
            //
            if (this.currentPage > 1) {
                //      
                // this.disabledNow = true
                this.$emit('askForPrePage')
            }
        },
        moveNext: function () {
            //
            if (this.currentPage < this.totalPage) {
                //      
                // this.disabledNow = true
                this.$emit('askForNextPage')
            }
        },
        moveUp: function () {
            // 
            // bug     
            //  
            let self = this
            self.currentIndex = self.currentIndex - 1
            console.log(self.currentIndex)
            if (self.currentIndex === -1) {
                if (self.currentPage > 1) {
                    //      
                    // this.disabledNow = true
                    self.$emit('askForPrePage')
                }
            }
            // self.website2 = self.pageData[self.currentIndex]['rootUrl']
            // console.log(self.currentIndex,self.website2)
        },
        moveDown: function () {
            // 
            // bug     
            //  
            let self = this
            self.currentIndex = self.currentIndex + 1
            console.log(self.currentIndex)
            if (self.currentIndex === self.dataLength) {
                if (self.currentPage < self.totalPage) {
                    //      
                    // self.disabledNow = true
                    self.$emit('askForNextPage')
                }
            }
            // self.website2 = self.pageData[self.currentIndex]['rootUrl']
            // console.log(self.currentIndex,self.website2)
        }
    }
}
</script>

<style scoped>
.Urls-seeDetail-iframe {
    height: 600px;
    width: 90%
}

.Urls-seeDetail-main {
    display: flex
}

.Urls-seeDetail-operate {
    display: flex;
    flex-direction: column;
}

.Urls-seeDetail-operate-button {
    margin: 20px
}

.Urls-seeDetail-operate .speccial {
    margin-top: 50px
}

.Urls-seeDetail-operate .speccial2 {
    margin-top: 50px
}
</style>
